import { Carousel, WingBlank } from 'antd-mobile';
import React from 'react';
import './Sbe.css'
class Carousels extends React.Component {
  state = {
    data: [
      'https://img.alicdn.com/imgextra/i4/6000000002436/O1CN01r0sGKc1TrjRkVM8w8_!!6000000002436-0-octopus.jpg',
      'https://img.alicdn.com/imgextra/i1/6000000000720/O1CN01QON2dX1HBnnbupK0g_!!6000000000720-0-octopus.jpg', 
      'https://aecpm.alicdn.com/simba/img/TB183NQapLM8KJjSZFBSutJHVXa.jpg',
      'https://aecpm.alicdn.com/simba/img/TB1XotJXQfb_uJkSnhJSuvdDVXa.jpg',
      'https://aecpm.alicdn.com/simba/img/TB1JNHwKFXXXXafXVXXSutbFXXX.jpg'
    ],
    imgHeight: 176,
  }
  componentDidMount() {
    // simulate img loading
    setTimeout(() => {
      this.setState({
        data: [
           'https://img.alicdn.com/imgextra/i4/6000000002436/O1CN01r0sGKc1TrjRkVM8w8_!!6000000002436-0-octopus.jpg',
           'https://img.alicdn.com/imgextra/i1/6000000000720/O1CN01QON2dX1HBnnbupK0g_!!6000000000720-0-octopus.jpg', 
           'https://aecpm.alicdn.com/simba/img/TB183NQapLM8KJjSZFBSutJHVXa.jpg',
           'https://aecpm.alicdn.com/simba/img/TB1XotJXQfb_uJkSnhJSuvdDVXa.jpg',
           'https://aecpm.alicdn.com/simba/img/TB1JNHwKFXXXXafXVXXSutbFXXX.jpg'
        ],
      });
    }, 100);
  }
  render() {
    return (<div id='a'>
      <WingBlank>
        <Carousel
          autoplay
          autoplayInterval={2000}
          infinite
        //   beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
        //   afterChange={index => console.log('slide to', index)}
        >
          {this.state.data.map(val => (
            <a
              key={val}
            //   href="http://www.alipay.com"
              style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
            >
              <img
                src={`${val}`}
                alt=""
                style={{ width: '100%', verticalAlign: 'top' }}
                onLoad={() => {
                  // fire window resize event to change height
                  window.dispatchEvent(new Event('resize'));
                  this.setState({ imgHeight: 'auto' });
                }}
              />
            </a>
          ))}
        </Carousel>
      </WingBlank>
      </div>
    );
  }
}

export default Carousels 