import React from 'react';
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch, MemoryRouter } from 'react-router-dom'
import Header from './pages/Header';
import routes from './router.config';
import {Button,TabBar} from 'antd-mobile'
import Mytab from './taobao/Mytab';
import Home from './taobao/Home';
import Cart from './taobao/Cart';
import Friend from './taobao/Friend';
import My from './taobao/My';
import SearchB from './taobao/SearchBar';
import Search from './taobao/Search';
import Buy from './taobao/Buy'

const App = ()=>{
	return <Router>
		<Switch>
			<Route exact path='/' component={Home} />
			<Route path='/cart' component={Cart} />
			<Route path='/friend' component={Friend} />
			<Route path='/my' component={My} />
			<Route path='/buy' component={Buy} />
			<Route path='/Search' component={Search} />
			<Route exact path='/SearchB' component={SearchB}></Route>
			<Route render={()=><Redirect to='/'/>} />
		</Switch>
		
	</Router>
}
export default App;


