import React from 'react';
import {BrowserRouter as Router,Redirect,Route,Link,Switch} from 'react-router-dom';
import './Sbe.css';
import { Grid } from 'antd-mobile';

const data1 = Array.from(new Array(10)).map(() => ({
  icon: 'https://img.alicdn.com/imgextra/i4/403373532/TB2.SsFyNlmpuFjSZPfXXc9iXXa_!!403373532.jpg_640x0q80_.webp',
}));

const GridExample = () => (
  <div >
    <div className="sub-title" style={{height:'40px',backgroundColor:'rgba(0,0,0,.05)',textAlign:'center',
         color:"rgb(255,78,34)",

    }}>你可能还喜欢</div>
    <Grid data={data1} columnNum={2} itemStyle={{ height: '280px',border:"8px solid rgba(0,0,0,.05)"}}
        renderItem={dataItem => (
            <div>
            <Link to='/Buy'><img src={dataItem.icon} style={{ width: '100%', height: '60%' }} alt="" />
            <div style={{fontSize: '10px', marginTop: '8px',textAlign:'left',lineHeight:'20px'}}>
                <span>法国小熊品家真空保温壶不锈钢保温桶食物罐焖烧壶饭盒</span>
            </div>
            <div style={{marginTop:'10px',textAlign:'left'}}>
            <span style={{color:'red',fontSize: '15px'}}>￥69</span>
             </div>
             </Link>
            </div>
        )}
    />
  </div>
);

export default GridExample;
